article {
  width: 100%;
  padding: 40px 24px;
}

.articleImage {
  object-fit: cover;
  object-position: 50% 30%;
  width: 100%;
  max-width: min(100%, 640px);
  height: 640px;
  display: flex;
}

.articleImage__mobile {
  width: 100dvw;
  max-width: 100dvw;
  height: 320px;
  margin: 96px -24px 0;
}

aside {
  display: none;
}

h2 small, h3 small {
  letter-spacing: .04em;
  opacity: .5;
  font-size: 24px;
  line-height: 64px;
}

.row {
  justify-content: center;
  display: flex;
  position: relative;
}

h1 {
  letter-spacing: -.03em;
  margin: 0 0 64px;
  font-size: 56px;
  font-weight: 600;
  line-height: 64px;
}

h2 {
  letter-spacing: -.04em;
  margin: 96px 0 0;
  font-size: 40px;
  font-weight: 300;
  line-height: 48px;
}

h2:first-child {
  margin: 0;
}

h3 {
  margin: 48px 0 0;
  font-size: 28px;
  line-height: 36px;
}

h4 {
  font-variant: all-small-caps;
  letter-spacing: .04em;
  opacity: .5;
  font-size: 28px;
  line-height: 36px;
}

p {
  margin: 24px 0 0;
  font-size: 20px;
  line-height: 32px;
}

.box {
  flex-direction: column;
  justify-content: center;
  width: min(100dvw, 640px);
  padding: 64px 24px;
  font-size: 24px;
  display: flex;
}

.boxTower {
  row-gap: 32px;
  display: grid;
}

@media screen and (width >= 768px) {
  article {
    flex: 3;
    width: calc(67vw - 72px);
    max-width: 960px;
    padding: 64px;
  }

  .articleImage__mobile, .desktopHidden {
    display: none;
  }

  aside {
    flex: 1;
    row-gap: 32px;
    max-width: 640px;
    display: grid;
    overflow: hidden;
  }

  h2 {
    max-width: 80%;
  }

  .box {
    height: 640px;
    padding: 0 32px 0 64px;
    font-size: 30px;
  }
}

.dim {
  opacity: .5;
}

.imageLink:hover {
  filter: brightness(.8);
}

